<template>
   <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path:'/home'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row>
        <!-- -->
        <el-col :span="8">
           <el-input placeholder="" v-model="order_id" clearable  @clear="clearSearchData">
              <el-button slot="append" icon="el-icon-search" @click="searchOrdersData"></el-button>
            </el-input>
        </el-col>
      </el-row>
      <!-- -->
      <el-table border :data="Orderlist" stripe width="100%">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="ID" prop="order_id" width="100px"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column label="" prop="order_price" width="100px"></el-table-column>
        <el-table-column label=""  width="100px">
          <template slot-scope="scope">
            <el-tag type="danger" v-if="scope.row.pay_status==='0'"></el-tag>
            <el-tag type="success" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send" width="100px"></el-table-column>
        <el-table-column label="" width="200px">
          <template slot-scope="scope">
            {{scope.row.create_time|dateFormat}}
          </template>
        </el-table-column>
        <el-table-column label="" width="150px">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" @click="editOrder(scope.row.order_number)"></el-button>
            <el-button type="success" icon="el-icon-location" @click="Progress(scope.row.consignee_addr)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- -->
      <el-pagination
              class = "dis-page"
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="orderQuery.pagenum"
              :page-sizes="[10, 20, 50, 100]"
              :page-size="orderQuery.pagesize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total">
            </el-pagination>
    </el-card>
      <!-- -->
      <el-dialog :visible.sync="orderEditdialogVisible" title="" width="40%" @close="closeDialog">
         <el-form label-width="100px" :model="CityOption" :rules="CityOptionRules" ref="CityOptionRef">
           <el-form-item label="/" prop="options">
            <el-cascader :options="citydata"  v-model="CityOption.options" clearable></el-cascader>
           </el-form-item>
           <el-form-item label="" prop="detail_addr">
             <el-input v-model="CityOption.detail_addr"></el-input>
           </el-form-item>
         </el-form>
         <span slot="footer" class="dialog-footer">
            <el-button @click="orderEditdialogVisible = false"> </el-button>
            <el-button type="primary" @click="orderEditdialogVisible = false"> </el-button>
          </span>
      </el-dialog>
      <!-- -->
      <el-dialog class="detail" :visible.sync="orderDetaidialogVisible" title="" width="40%" @close="closedetail">
        <el-row>
          <el-col :span="4">ID:</el-col>
          <el-col :span="20">{{searchOrder.order_id}}</el-col>
        </el-row>
        <el-row>
          <el-col :span="4">:</el-col>
          <el-col :span="20">{{searchOrder.order_number}}</el-col>
        </el-row>
        <el-row>
          <el-col :span="4">:</el-col>
          <el-col :span="20">{{searchOrder.order_price}}</el-col>
        </el-row>
        <el-row>
          <el-col :span="4">:</el-col>
          <el-col :span="20">{{searchOrder.is_send}}</el-col>
        </el-row>
        <el-row>
          <el-col :span="4">:</el-col>
          <el-col :span="20">
            <el-tag v-if="searchOrder.order_pay==='0'" type="danger"></el-tag>
            <el-tag v-else-if="searchOrder.order_pay==='1'" type="success"></el-tag>
            <el-tag v-else-if="searchOrder.order_pay==='2'" type="success"></el-tag>
            <el-tag v-else-if="searchOrder.order_pay==='3'" type="success"></el-tag>
            <span v-else></span>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="4">:</el-col>
          <el-col :span="20">{{searchOrder.consignee_addr}}</el-col>
        </el-row>
        <el-row>
          <el-col :span="4">:</el-col>
          <el-col :span="20">
              <el-tag type="danger" v-if="searchOrder.pay_status===0"></el-tag>
              <el-tag type="success" v-lese></el-tag>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="4">:</el-col>
          <el-col :span="20" v-model="searchOrder.goods">
            <template slot-scope="scope">
              {{scope.row}}
            </template>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="4">:</el-col>
          <el-col :span="20">{{searchOrder.consignee_addr}}</el-col>
        </el-row>
        <span slot="footer" class="dialog-footer">
           <el-button @click="orderDetaidialogVisible = false" type="primary"></el-button>
         </span>
      </el-dialog>
      <!-- -->
      <el-dialog :visible.sync="ProgressDialogVisible" title="">
        <el-timeline>
            <el-timeline-item
              v-for="(activity, index) in timeLineArr"
              :key="index"
              :color="activity.color"
              :type="activity.type"
              :timestamp="activity.ftime">
              {{activity.context}}
            </el-timeline-item>
          </el-timeline>
      </el-dialog>
   </div>
</template>

<script>
  import citydata from './city_data2017_element.js'
  export default{
    data(){
      return {
       orderQuery:{
         query:'',//
         pagenum:1,//
         pagesize:10,//
         user_id:'',//id
         pay_status:'',//
         is_send:'',//
         order_fapiao_title:'',//
         order_fapiao_company:'',//
         order_fapiao_content:'',//
         consignee_addr:''//
       },
       order_id:'',//id
       //
       citydata:citydata,
       CityOption:{
         options:[],
         detail_addr:''
       },
       total:0,//
       Orderlist:[],//
       orderEditdialogVisible:false,//
       orderDetaidialogVisible:false,//
       ProgressDialogVisible:false,//
       CityOptionRules:{
         options:[{required:true,trigger:'blur',message:''}],
         detail_addr:[{required:true,trigger:'blur',message:''}]
       },
       searchOrder:{},//id
       timeLineArr:[]//
      }
    },
    created(){
      this.initOrdersData()
    },
    methods:{
      //
      async initOrdersData(){
        const {data:res} = await this.$http.get('orders',{params:this.orderQuery})
        console.log(res)
        if(res.meta.status == 200){
          this.Orderlist = res.data.goods;
          this.total = res.data.total;
        }
        else{
          return this.$message.error(res.meta.msg)
        }
      },

      //
      clearSearchData(){
        this.searchOrder = {}
      },
      closedetail(){
        this.searchOrder = {}
      },
      //
      async Progress(){
        const {data:res} = await this.$http.get('kuaidi/804909574412544580')//
        console.log(res)
        if(res.meta.status == 200){
          this.timeLineArr = res.data;
          this.timeLineArr[0].color='#0bbd87'
        }
        else{
          return this.$message.error(res.meta.msg)
        }
        this.ProgressDialogVisible = true
      },
      //
      closeDialog(){
        this.$refs.CityOptionRef.resetFields()
      },
      //
      handleSizeChange(newSize){
        //console.log(newSize)
        this.orderQuery.pagesize = newSize
        this.initOrdersData()
      },
      //
      handleCurrentChange(newPage){
        //console.log(newPage)
        this.orderQuery.pagenum = newPage
        this.initOrdersData()
      },
      //id
      async searchOrdersData(){
        this.orderDetaidialogVisible = true;
        const {data:res} = await this.$http.get(`orders/${this.order_id-0}`)
        console.log(res)
        if(res.meta.status == 200){
          this.searchOrder = res.data
        }
        else{
          return this.$message.error(res.meta.msg)
        }
      },
      editOrder(){
        this.orderEditdialogVisible = true
      },
    }
  }
</script>

<style lang="less" scoped="scoped">
  .dis-page{
    margin-top: 15px;
  }
  .el-cascader{
    width: 100%;
  }
  .detail{
    .el-row{
      line-height: 40px;
    }
  }
</style>
